import React, { useState, useEffect } from "react";
import RotateLoader from "react-spinners/RotateLoader";
import Top from '../components/Top'
import Search from '../components/Search'
import Profile from '../components/Profile'
import Input from '../components/Input'


const Home = () => {
  const [top, setTop] = useState([]);
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [notFound, setNotFound] = useState('')

  const search = (keyword) => {
    fetch(`https://api.jikan.moe/v3/search/anime?q=${keyword}&page=1`)
      .then((res) => res.json())
      .then((data) => {
        if (data.results) {
          setResults(data.results);
          setNotFound('')
          window.scrollTo(0, 1200);
        } else {
          setNotFound('Invalid Keyword!')
        }
      });
  }


  useEffect(() => {
    setLoading(true);
    fetch(`https://api.jikan.moe/v3/top/anime/1/airing`)
      .then(res => res.json())
      .then(data => {
        setTop(data.top.slice(0, 5));
        setLoading(false);
      });
  }, []);


  return (
    <React.Fragment>
      <header className="top-header">Profile</header>
      <Profile />
      <header className="top-header">Top airing anime</header>
      <div className="anime-wrapper">
        {loading ? (
          <div className="loader">
            <RotateLoader color={"#3895d3"} loading={loading} size={10} />
          </div>
        ) : (
          <React.Fragment>
            {top.map((a, i) => <Top anime={a} key={i} />)}

            <div className="input">
              <Input callback={search} placeholder="Search anime . . ." />
              <p>{notFound}</p>
            </div>

          </React.Fragment>
        )}
      </div>

      <div className="anime-wrapper">
        {results.map((a, i) => <Search anime={a} key={i} />)}
      </div>

    </React.Fragment>
  );
}

export default Home
