import React from 'react'

var url = 'https://myanimelist.net/profile/Naxocist';

function Profile() {
    return (
        <div className="profile">
            <a href={url} target="_blank" className="profile-url" rel="noreferrer">
                <img className="profile-pic" src='http://cdn.akamai.steamstatic.com/steamcommunity/public/images/avatars/47/472631d83da20301f44b5fa489e6881824523132_full.jpg'
                    alt="Naxocist's profile" />
            </a>
            <div className="info">
                <ul>
                    <li>Naxocist</li>
                    <li>Date of birth : 9<sup>th</sup> August 2006</li>
                    <li>Favorite : Little Witch Academia</li>
                </ul>
            </div>
        </div>
    )
}

export default Profile
