import React, { useState, useEffect } from "react";
import Poster from "../components/Poster";
import Banner from "../components/Banner";
import Input from "../components/Input";
import SyncLoader from "react-spinners/SyncLoader";

const axios = require("axios");
const jikan = require("jikanjs");


const isDuplicate = (data, obj) =>
  data.some((el) =>
    Object.entries(obj).every(([key, value]) => value === el[key])
  );


const Library = () => {
  const [posters, setPosters] = useState([]);
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const [existed, setExisted] = useState('');

  const addPoster = (title) => {
    jikan.search("anime", title).then((res) => {
      const anime = res.results[0];
      const name = anime.title;
      const href = anime.url;
      const img = anime.image_url;
      const data = { name, img, href };
      if (!isDuplicate(posters, data)) {
        setPosters([data, ...posters]);
        setExisted('')
      } else {
        setExisted('Existed in the list!')
      }
      axios.post("https://mysql-animes.herokuapp.com/add", data);
    });
  };


  const deletePoster = (name) => {
    setPosters(posters.filter((poster) => poster.name !== name));
    setExisted('');
    axios
      .delete(`https://mysql-animes.herokuapp.com/remove/${name}`)
      .then(() => { });
  };




  const backToTop = () => {
    window.scrollTo(0, 0);
  }


  const toggleVisible = () => {
    const scrolled = document.documentElement.scrollTop;
    if (scrolled > 600) {
      setVisible(true);
    } else if (scrolled <= 600) {
      setVisible(false);
    }
  }

  window.addEventListener('scroll', toggleVisible)

  useEffect(() => {
    setLoading(true);
    fetch(`https://mysql-animes.herokuapp.com/animes`)
      .then((res) => res.json())
      .then((data) => {
        setPosters(data.reverse());
        setLoading(false);
      });
  }, []);

  return (
    <React.Fragment>
      <div className="jump" style={{ opacity: visible ? 1 : 0 }}>
        <img src="https://cdn3.iconfinder.com/data/icons/faticons/32/arrow-up-01-512.png"
          onClick={backToTop} alt="arrow"></img>
      </div>

      <Banner img="https://c.wallhere.com/photos/6c/bb/Little_Witch_Academia-284456.jpg!d" />
      <div className="input">
        <Input callback={addPoster} placeholder="Add anime . . ." />
        <p>{posters.length} animes in this library..</p>
        <p>{existed}</p>
      </div>

      <div className="anime-wrapper">
        {loading ? (
          <div className="loader">
            <SyncLoader
              color={"#3895d3"}
              loading={loading}
              size={10}
              margin={20}
            />
          </div>
        ) : (
          <React.Fragment>
            {posters.map((poster, i) => (
              <Poster
                name={poster.name}
                href={poster.href}
                img={poster.img}
                key={i}
                deletePoster={deletePoster}
              />
            ))}
          </React.Fragment>
        )}
      </div>
    </React.Fragment>
  );
}

export default Library